.header {
  &__container {
    padding: 0 10px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    border-bottom: 1px solid var(--accent-color);
  }

  &__socials {
    display: flex;
    column-gap: 8px;
  }

  &__socials-item {
    display: flex;
    column-gap: 4px;
    align-items: center;
  }

  &__socials-link {
    display: flex;
    align-items: center;
    column-gap: 4px;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }

  &__contacts {
    display: flex;
    column-gap: 24px;
  }

  &__contacts-item {
    display: flex;
    column-gap: 4px;
    align-items: center;
    font-weight: 600;
    font-size: 16px;
  }

  &__contacts-link {
    display: flex;
    column-gap: 4px;
    align-items: center;
    transition: color 0.3s;
    font-weight: 600;
    font-size: 16px;

    &:hover {
      color: var(--purple-color);
    }
  }

  &__right {
    display: flex;
    align-items: center;
    column-gap: 24px;
  }

  &__search {
    svg {
      circle,
      path {
        transition: all 0.3s;
      }
    }

    &:hover {
      svg {
        circle,
        path {
          stroke: var(--purple-color);
        }
      }
    }
  }

  &__bvi {
    border-radius: 60px;
    padding: 10px 24px;
    background-color: rgba(229, 57, 53, 0.2);
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--red-color);
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    transition: color 0.3s;

    svg {
      path {
        transition: all 0.3s;
      }
    }

    &:hover {
      color: var(--purple-color);

      svg {
        path {
          fill: var(--purple-color);
        }
      }
    }
  }

  &__bot {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px 0;
  }

  &__logo {
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }

  &__bnts {
    display: flex;
    column-gap: 16px;
  }

  &__consultation,
  &__appointment {
    border-radius: 40px;
    padding: 12px 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.25;
    text-transform: uppercase;
    text-align: center;
  }

  &__consultation {
    background: rgba(127, 115, 187, 0.2);
    color: var(--purple-color);
    transition:
      background-color 0.3s,
      color 0.3s;

    &:hover {
      background: var(--accent-color);
      color: var(--white-color);
    }
  }

  &__appointment {
    background: var(--purple-color);
    color: var(--white-color);
    transition:
      background-color 0.3s,
      color 0.3s;

    &:hover {
      background: var(--accent-color);
    }
  }
}
